/* 초기화 */
* {
    margin: 0;
    padding: 0;
    font-family: SUITE-Regular;
}

ul,li,ol {
    list-style: none;
}
a {
    text-decoration: none;
    color: #111;
}
input,
textarea {
    outline: none;
}
#root > div > h3 {
    font-family: ONE-Mobile-POP;
    margin-top:30px;
}
strong {
    font-family: ONE-Mobile-POP;
    font-weight: normal;
    color: #FF5B2D;
}
#wrapper {
    width: 100%;
}
.brandIntro::before {
    background-image: url(../../images/brand/intro.png);
    
}

.container {padding-bottom: 20px;}

.link_menu {
    position: fixed;
    right:1.5rem;
    z-index: 25;
    bottom:calc(100% - 700px);
}
